/* ============================
   Full Page Background
   ============================ */
   .form-page {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(to bottom, #001f3f 0%, #003366 70%, #006699 100%);
    position: relative;
    font-family: 'Arial', sans-serif;
    overflow: hidden;
  }
  
  /* Subtle animated wave overlay */
  .form-page::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 200%;
    height: 200%;
    background: url('https://i.ibb.co/2c5YXs0/fish.png') repeat-x;
    background-size: cover;
    opacity: 0.08;
    animation: waveMove 30s linear infinite;
    pointer-events: none;
    z-index: 0;
  }
  
  @keyframes waveMove {
    0% { transform: translateX(0); }
    100% { transform: translateX(-50%); }
  }
  
  /* ============================
     Form Container
     ============================ */
  .form-container {
    position: relative;
    z-index: 1;
    max-width: 500px;
    width: 90%;
    background: rgba(0, 51, 77, 0.95);
    padding: 40px 30px;
    border-radius: 15px;
    text-align: center;
    color: white;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.5);
  }
  
  /* Heading */
  .form-container h2 {
    font-size: 2rem;
    margin-bottom: 25px;
    text-shadow: 1px 1px 4px rgba(0,0,0,0.4);
  }
  
  /* ============================
     Labels & Inputs
     ============================ */
  label {
    display: block;
    text-align: left;
    font-weight: bold;
    margin-top: 15px;
    margin-bottom: 5px;
    font-size: 1.1rem;
  }
  
  .form-container select,
  .form-container input[type="text"],
  .form-container input[type="file"],
  .form-container textarea {
    width: 100%;
    padding: 12px;
    margin-bottom: 15px;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    background: #f9f9f9;
    color: #333;
  }
  
  /* Textarea */
  textarea {
    min-height: 120px;
    resize: vertical;
  }
  
  /* Focus styles */
  .form-container input:focus,
  .form-container select:focus,
  .form-container textarea:focus {
    outline: none;
    box-shadow: 0 0 8px #00bfff;
    border: 1px solid #00bfff;
  }
  
  /* ============================
     Buttons
     ============================ */
  .btn {
    padding: 12px 25px;
    margin: 10px 5px 0 5px;
    background-color: #0077b6;
    color: white;
    border-radius: 25px;
    border: none;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.3s;
    text-decoration: none;
    display: inline-block;
  }
  
  .btn:hover {
    background-color: #00bfff;
    transform: translateY(-3px);
  }
  
  /* Back button spacing */
  .back-btn {
    margin-top: 20px;
  }
  
  /* ============================
     Floating Bubble Animation
     ============================ */
  .form-page::after {
    content: '';
    position: absolute;
    width: 15px;
    height: 15px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    animation: bubble 20s linear infinite;
    top: 90%;
    left: 10%;
    z-index: 0;
  }
  
  @keyframes bubble {
    0% { transform: translateY(0) scale(0.5); opacity: 0.3; }
    50% { transform: translateY(-200px) scale(1); opacity: 0.6; }
    100% { transform: translateY(-400px) scale(0.5); opacity: 0; }
  }
  